.nav{
    position:fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    /* animations*/
    transition-timing-function: ease-in;
    transition: all 0.5s;
}


.nav_black{
    background-color: black;
}

.nav_contents{
    display: flex;
  justify-content: space-between;
}

.nav_logo{
    position:fixed;
    top: 10;
    left: 0;
    width: 80px;
    cursor: pointer;
    margin-left: 20px;
    object-fit: contain;

}

.nav_avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
